*{
	outline: none !important;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	&:active{
		outline: none !important;
	}
}

.cont1{
	max-width: 1780px;
	margin-left: auto;
	margin-right: auto;
}


img{
	display: block;
	max-width: 100%;
	height: auto;
}

a,
button{
	transition: all .3s ease;
}

a{
	text-decoration: underline;
	// color: $c-blu;
	&:hover{
		text-decoration: none;
	}
}

.icon {
  display: inline-block;
}

.tbl{
	width: 100%;
	display: table;
}

.tbl-cell{
	display: table-cell;
}

.tbl-row{
	display: table-row;
}

.kd-tbl{
	width: 100%;
	display: table;
}


.s{
	padding-top: 60px;
	padding-bottom: 60px;
}

p{
	margin-bottom: 0;
}

ul{
	margin-bottom: 0;
}

h1{
	font-size: 34px;
	margin-bottom: 30px;
}

h3{
	font-size: 18px;
	font-size: 18px;
}

.sect-title{}

.content{
	p{
		margin-bottom: 20px;
		line-height: 1.7;
	}
	h1{
		font-size: 24px;
		font-weight: bold;
		margin-bottom: 15px;
	}
	h2{
		font-size: 18px;
		font-weight: bold;
		// font-family: $f-o-s;
		margin-bottom: 25px;
	}
	h3{
		font-size: 16px;
		// font-family: $f-o-s;
		margin-bottom: 20px;
	}
	h4{
		font-size: 14px;
		// font-family: $f-o-s;
		margin-bottom: 20px;
	}
	h6{
		font-size: 16px;
		margin-bottom: 20px;
	}
	img{
		margin-bottom: 30px;
	}
	.gallery{
		img{
			margin-bottom: 0;
		}
	}
	blockquote{
		padding: 30px 30px 15px;
		// background-color: $c-gy;
		position: relative;
		&:after{
			content:'';
			display: block;
			width: 18px;
			height: 16px;
			position: absolute;
			left: 10px;
			top: 10px;
			z-index: 2;
			background: url('../img/quotes.png') center no-repeat;
		}
	}
	ul{
		padding-left: 0px;
		li{
			list-style-type: none;
			margin-bottom: 7px;
			padding-left: 20px;
			position: relative;
			&:before{
				content: "";
				width: 13px;
				height: 12px;
				display: block;
				background: url('../img/birdd.png') center no-repeat;	
				position: absolute;
				left: 0;
				top: 4px;	
			}
		}
	}
	
	ol{
		padding-left: 15px;
		li{
			margin-bottom: 7px;
		}
	}

}


hr{
	margin-bottom: 30px;
}

.morre{
	// color: $c-yw;
	font-size: 12px;
}